.to-do-container {
    width: 500px;
    height: 65px;
    background-color: #234E9E;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
    border: 1px solid white;
    cursor: pointer;
}

.to-do-container:nth-child(3n + 1) {
   background-color: #234E9E;
}

.to-do-container:nth-child(3n + 2) {
    background-color: #39568b;
}

.to-do-container:nth-child(3n + 3){
    background-color: #334360;
}

.completada {
    background-color: #00112e;
    text-decoration: line-through;
}

.to-do-text {
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
}

.to-do-icon-container {
    display: flex;
    align-items: center;
}

.to-do-icon {
    width: 25px;
    height: 25px;
    margin: 5px;
}